import axios from '../http'
export default {
    state: {
        show: true,
        userdata: {

        }
    },

    //mutationsstatemutations 
    mutations: {
        //
        SWITCH_LOGIN(state) {
            //statestate
            if (state.userdata) {
                state.show = state.show ? false : true;
            }
            //state
        },

        //
        USER_LOGIN(state, arg) {
            state.userdata = arg
        },
    },

    //
    actions: {
        login({ commit }, userName, passWord) {
            axios.post(
                '/v1/login',
                {
                    userName,
                    passWord
                }
            )
                .then(response => {
                    //response.data
                    commit('USER_LOGIN', { userName: '', postsNum: '100', headImg: 'https://ss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=2564997198,4187947589&fm=58', })
                    //var storage = window.localStorage
                    //JSONJSON
                    // let form = JSON.stringify(this.form)
                    // storage.data = form
                    // console.log(typeof storage.data)
                    //storage.username = ''
                    //JSONJSON
                    // let json = storage.data
                    // let jsonObj = JSON.parse(json)
                    // console.log(typeof jsonObj)
                })
            commit('SWITCH_LOGIN')
        },
    }
}


