<template>
    <div class="register">
        <div class="header">
            <div class="btn_back">
                <el-button type="text" :circle="true" @click="$store.commit('SWITCH_REGISTER')"></el-button>
            </div>
        </div>

        <div class="tabs">
            <el-tabs>
              <el-tab-pane label="">
                <div class="table">
                    <el-form :rules="rules" :model="msg" ref="msg" class="demo-ruleForm">
                    <el-form-item label="" prop="zhanghao">
                      <el-tooltip class="item" effect="light" content="5-20" placement="right">
                        <el-input v-model="msg.zhanghao" placeholder="" maxlength="20" autofocus="true"></el-input>
                      </el-tooltip>  
                    </el-form-item>
                    <el-form-item label="" prop="password">
                      <el-tooltip class="item" effect="light" content="6-20" placement="right">
                        <el-input v-model="msg.password" placeholder="" type="password" maxlength="20"></el-input>
                      </el-tooltip>
                    </el-form-item>
                    <el-form-item label="" prop="re_password">
                        <el-input v-model="msg.re_password" placeholder="" type="password" maxlength="20"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="realname">
                      <el-tooltip class="item" effect="light" content="24" placement="right">
                        <el-input v-model="msg.realname" placeholder=""></el-input>
                      </el-tooltip>
                    </el-form-item>
                    <el-form-item label="" prop="identity_card">
                      <el-tooltip class="item" effect="light" content="" placement="right">
                        <el-input v-model="msg.identity_card" placeholder=""></el-input>
                      </el-tooltip>
                    </el-form-item>
                    <el-form-item label="" prop="">
                        <el-input v-model="msg.verification" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-radio label=""></el-radio><a href="http://www.baidu.com" target="_blank"></a>
                    </el-form-item>
                    <el-form-item label="">
                        <el-button type="warning" @click="axiosRegister('msg')" ></el-button>
                    </el-form-item>              
                    <el-form-item label="">
                        <el-button type="text" @click="$store.commit('SWITCH_REGISTER')"></el-button>
                    </el-form-item>
                    </el-form>
                </div>
              </el-tab-pane>

              <el-tab-pane label="" >
                <div class="table">
                    <el-form :rules="rules">
                    <el-form-item label="" prop="phone">
                        <el-input v-model="msg.phone_number" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="msg.verification" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="msg.phone_verification" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="msg.password" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="msg.re_password" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="msg.realname" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="msg.identity_card" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-radio label=""></el-radio><a href="http://www.baidu.com" target="_blank"></a>
                    </el-form-item>
                    <el-form-item label="">
                        <el-button type="warning" @click="axiosRegister" :loading="openLoading"></el-button>
                    </el-form-item>              
                    <el-form-item label="">
                        <el-button type="text" @click="$store.commit('SWITCH_REGISTER')"></el-button>
                    </el-form-item>
                    </el-form>
                </div>                  
              </el-tab-pane>
            </el-tabs>  
        </div>

        <div class="clear">

        </div>

    </div>
</template>

<script>
    export default {
        data() {
            // js
            var validatePhone = (rule, value, callback) => {
                let reg = /^[1][3,4,5,7,8][0-9]{9}$/
                value = this.msg.phone_number.replace(/\s+/g,'')
                if (!value) {
                    return callback(new Error('!!'))
                }
                setTimeout(() => {
                    if (!reg.test(value)) {
                        callback(new Error(''))
                    } else {
                        callback()
                    }
                }, 1000)
            }

            // js
            var validateZhanghao = (rule,value,callback) => {
                let reg = /^[a-zA-Z][0-9a-zA-Z]{4,19}$/
                value = this.msg.zhanghao.replace(/\s+/g,'')
                if(!value) {
                    return callback(new Error('!!'))
                }
                setTimeout(() => {
                    if(!reg.test(value)){
                        callback(new Error(""))
                    }else{
                        callback()
                    }
                },1000)

            }


            // js
            var validatePassWord = (rule,value,callback) => {
                let reg1 = /\s/
                let reg2 = /^(?:\d+|[a-zA-Z]+|[!@#$%^&*]+){6,20}$/
                value = this.msg.password.replace(/\s+/g,'')
                if(!value) {
                    return callback(new Error('!!'))
                }
                setTimeout(() => {
                    if(reg1.test(value)){
                        callback(new Error(""))
                    }
                    if(!reg2.test(value)){
                        callback(new Error(""))
                    }else{
                        callback()
                    }
                },1000)
            }

            // js
            var validateRePassWord = (rule,value,callback) => {
                value = this.msg.re_password
                if(!value) {
                    return callback(new Error('!!'))
                }
                setTimeout(() => {
                    if(value != this.msg.password){
                        callback(new Error(""))
                    }else{
                        callback()
                    }
                },1000)
            }
            

             // js
            var validateRealname = (rule,value,callback) => {
                let reg = /^[\u4e00-\u9fa5]{2,4}$/
                value = this.msg.realname.replace(/\s+/g,'')
                if(!value) {
                    return callback(new Error('!!'))
                }
                setTimeout(() => {
                    if(!value===this.msg.password) {
                        callback(new Error(""))
                    }else{
                        callback()
                    }
                },1000)
            }

             // js
            var validateIdentityCard = (rule,value,callback) => {
                let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
                value = this.msg.identity_card.replace(/\s+/g,'')
                if(!value) {
                    return callback(new Error('!!'))
                }
                setTimeout(() => {
                    if(!reg.test(value)){
                        callback(new Error(""))
                    }else{
                        callback()
                    }
                },1000)
            }

            return {
                msg: {
                    zhanghao: '',
                    password: '',
                    re_password: '',
                    realname: '',
                    identity_card: '',
                    verification: '',
                    phone_verification: '',
                    phone_number:'',
                },
                openLoading: false,
                rules: {
                    zhanghao: [
                        { validator: validateZhanghao, trigger: 'blur'}
                    ],
                    password: [
                        { validator: validatePassWord, trigger: 'blur'}
                    ],
                    re_password: [
                        { required: true, message: '', trigger: 'change' },
                        { validator: validateRePassWord, trigger: 'blur'}
                    ],
                    realname: [
                        { validator: validateRealname, trigger: 'blur'}
                    ],
                    identity_card:[
                        { validator: validateIdentityCard, trigger: 'blur'}
                    ],
                    phone: [{ required: true, validator: validatePhone, trigger: 'blur' }],    
                }
                
            }
        },
        methods: {
            axiosRegister(formName){
                this.$refs[formName].validate(valid => {
                    if (valid) {
                        this.$http.post('/v1/register',{
                            zhanghao: this.msg.zhanghao,
                            password: this.msg.password,
                            realname: this.msg.realname,
                            identity_card: this.msg.identity_card,
                            phone_number: this.msg.phone_number            
                        })
                        .then(response => {
                            //code200
                            if(response.status == 200){
                                if(response.data == '0'){
                                    console.log("")
                                    this.$store.commit("SWITCH_REGISTER")
                                }
                                else{
                                    alert("")
                                    return false
                                }
                            }
                        })
                    }
                    else {
                        console.log('error submit!!')
                        return false
                    }
                })
                
            }
        }
        
    }
</script>

<style scoped>
.register{
    width: 405px;
    margin: 5% auto;
}
.header{
    height: 50px;
    background-image: url('../assets/register_img/register_head.png');
}
.btn_back{
    float: right;
    margin-right: 16px;
    margin-top: 12px;
}
.tabs{
    background: #f0f0f0;
    height: 590px;
}
.table{
    margin: 0 auto;
    width: 300px;

}
.clear{
    clear: both;
}

</style>


