<template>
    <div>
        <el-form v-if="$store.state.login.show">
            <div class="form">
            <p><strong></strong></p>
            <el-form-item>
                <el-row>
                    <el-col :span="16">
                        <el-input placeholder="" v-model="username"></el-input>
                        <el-input placeholder="" v-model="password"></el-input>
                    </el-col>
                    <el-col :span="8">
                        <el-button class="button" style="height:80px" @click="login">
                            <span style="color:white;font-size:18px"><strong></strong></span>
                        </el-button>
                    </el-col>
                </el-row>
            </el-form-item>

            <el-form-item>
                <el-col :span="12">
                    <el-radio></el-radio>
                </el-col>
                <el-col :span="12">
                    <el-button type="text"></el-button>
                </el-col>
            </el-form-item>

            <el-form-item>
                <el-button type="success" @click="$store.commit('SWITCH_REGISTER')"></el-button>
            </el-form-item>

            <el-form-item>
                <span></span>
            </el-form-item>

            </div>
        </el-form>

        <el-form :model="form" v-else>
            <div class="form">
                <p><strong></strong></p>
                <el-form-item>
                    <el-col :span="12"><img :src="form.headImg" style="width:80px"></el-col>
                    <el-col :span="12"><h3>{{form.userName}}</h3></el-col>
                </el-form-item>
                <el-form-item label="">{{form.postsNum}}</el-form-item>
                <el-form-item label="">{{form.myReply}}</el-form-item>
                <el-form-item label="">{{form.receivedReply}}</el-form-item>
                <el-form-item label="">{{form.myCollect}}</el-form-item>
            </div>
        </el-form>
    </div>

</template>

<script>
    export default {
        data() {
            return {
                username: '',
                password: '',
            }
        },
        //axiosaxios
        computed: {
            form: function() {
                return this.$store.state.login.userdata || ''
            }
        },


        //
        //beforeMount: function(){

            // if(window.localStorage.username)
            // {
            //     this.form.userName = window.localStorage.username            
            // }            
        //},

        // //localStorage
        // beforeUpdate: function() {
        //     if(window.localStorage.username)
        //     {
        //         this.form.userName = window.localStorage.username             
        //     }
        // },

        methods: {
            login(){
                //vuex
                this.$store.dispatch('login',this.username,this.password)
            },

        },

  }
    
</script>

<style scoped>
.form{
    padding: 20px;
    margin: 10px;
    width: 230px;
    background-color: white;
}
.button{
    background-color: rgb(245, 101, 35);
}
</style>


