//axios
import axios from 'axios'
import qs from 'qs'

//
let cancel ,promiseArr = {}
const CancelToken = axios.CancelToken;
//
axios.interceptors.request.use(config => {
    //
    if (promiseArr[config.url]) {
        promiseArr[config.url]('')
        promiseArr[config.url] = cancel
    } else {
        promiseArr[config.url] = cancel
    }
      return config
}, error => {
    return Promise.reject(error)
})

//
axios.interceptors.response.use(response => {
    return response
}, err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break;
        case 401:
          err.message = ''
          break;
        case 403:
          err.message = ''
          break;
        case 404:
          err.message = ','
          break;
        case 405:
          err.message = ''
          break;
        case 408:
          err.message = ''
          break;
        case 500:
          err.message = ''
          break;
        case 501:
          err.message = ''
          break;
        case 502:
          err.message = ''
          break;
        case 503:
          err.message = ''
          break;
        case 504:
          err.message = ''
          break;
        case 505:
          err.message = 'http'
          break;
        default:
          err.message = `${err.response.status}`
      }
    } else {
      err.message = ""
    }
    alert(err.message)
    return Promise.resolve(err.response)
})

//get,post
axios.defaults.baseURL = 'api'

axios.defaults.headers = {
    'X-Requested-With': 'XMLHttpRequest'
}
axios.defaults.timeout = 10000

export default {
    //get
    get (url,params) {
      return new Promise((resolve,reject) => {
        axios({
          method: 'get',
          url: url,
          params: params,
          cancelToken: new CancelToken(c => {
            cancel = c
          })
        }).then(res => {
          resolve(res)
        })
      })
    },

    //post
    post (url,data) {
      return new Promise((resolve,reject) => {
        axios({
          method: 'post',
          url: url,
          data: qs.stringify(data),//post
          headers: {
            'X-Requested-With': 'XMLHttpRequest',
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },
          cancelToken: new CancelToken(c => {
            cancel = c
          })
        }).then(res => {
          resolve(res)
        })
      })
     }
  }


